
.box {
  position: absolute;
  width: 1rem;
  height: 1rem;
  top: 60px;
  left: 60px;
  transform-style: preserve-3d;
}
.face {
  position: absolute;
  top: 0;
  left: 0;
  width: 1rem;
  height: 1rem;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, .1);
  line-height: 1rem;
  background: rgba(158,158,158,.5);
  font-size: 20px;
  text-align: center;
}
.face:nth-child(1) {
  transform: translateZ(.5rem);
}
.face:nth-child(2) {
  transform: translateX(.5rem) rotateY(90deg);
}
.face:nth-child(3) {
  transform: translateY(.5rem) rotateX(90deg);
}
.face:nth-child(4) {
  transform: translateX(-.5rem) rotateY(90deg);
}
.face:nth-child(5) {
  transform: translateY(-.5rem) rotateX(90deg);
}
.face:nth-child(6) {
  transform: translateZ(-.5rem);
}



